@import "./global.scss";

.App {
  height: 100vh;

  * {
    overflow-x: hidden;
  }

  .sections {
    width: 100%;
    height: calc(100vh - 80px);
    background-color: white;
    position: relative;
    top: 80px;
    scroll-behavior: smooth;
    // scroll-snap-type: y mandatory;
    scrollbar-width: none; // for firefox
    &::-webkit-scrollbar {
      // for chrome and other browsers
      display: none;
    }

    > * {
      width: 100vw;
      height: calc(100vh - 80px);
      scroll-snap-align: start;
    }

    > :not(:first-child) {
      padding-top: 20px;
    }
  }
}
