@import "../../global.scss";

.work {
  background-color: $themeColor;
  @include flexCenter;
  position: relative;

  .arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;

    @include mobile {
      height: 20px;
    }

    &.left {
      left: 5px;
      transform: rotateY(180deg);

      @include sm {
        left: 20px;
      }
    }
    &.right {
      right: 5px;

      @include sm {
        right: 20px;
      }
    }
  }

  .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-in-out;

    .cont {
      width: 100vw !important;
      @include flexCenter;

      .item {
        width: 700px;
        height: 100%;
        background-color: white;
        border-radius: 20px;
        @include flexCenter;

        @include mobile {
          width: 80%;
          height: 80%;
        }

        .left {
          flex: 4;
          height: 80%;
          @include flexCenter;

          @include mobile {
            height: 90%;
          }

          .leftContainer {
            width: 90%;
            height: 70%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            h2 {
              font-size: 20px;
              overflow: hidden;

              @include mobile {
                font-size: 16px;
              }
            }
            #desc {
              font-size: 13px;
              overflow: hidden;

              @include mobile {
                // font-size: 10px;
                display: none;
              }
            }
            #date {
              font-size: 12px;
              font-weight: 600;

              @include mobile {
                font-size: 10px;
              }
            }
          }
        }

        .right {
          flex: 8;
          height: 100%;
          @include flexCenter;
          overflow: hidden;

          img {
            width: 400px;
            transform: rotate(-5deg);

            @include mobile {
              transform: rotate(0deg);
              margin-right: 5px;
            }
          }
        }
      }
    }
  }
}
